@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-style: normal;
  font-weight: 600;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Montserrat", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
  position: relative;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  padding: 10px 10px;
  width: 100%;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.5;
  transition: all 0.3s ease 0s;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
    box-shadow: 0px 10px 20px 5px rgba(0, 0, 0, 0.5);
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
  box-shadow: 0px 10px 20px 5px rgba(0, 0, 0, 0.5);
}
.btn:active {
  transform: translateY(1px);
  box-shadow: inset 0px 10px 20px 2px rgba(0, 0, 0, 0.25);
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}
@media (max-width: 767px) {
  .header__overlay {
    position: absolute;
    top: 0;
    left: 0;
    display: none;
    width: 100%;
    height: 100vh;
    background: rgba(0, 43, 102, 0.5);
    opacity: 1;
    z-index: 2;
  }
  .header__overlay._active {
    display: block;
  }
}
.header__container {
  transition: all 0.3s ease 0s;
  transform-origin: right top;
  min-width: 38px;
  display: flex;
  justify-content: space-between;
  padding-top: 35px;
  padding-bottom: 20px;
  color: #f9fbfd;
  z-index: 3;
}
@media (max-width: 1023px) {
  .header__container {
    padding-top: 20px;
  }
}
.header__container._active {
  transition: all 0.3s ease 0s;
  transform-origin: right;
}
@media (max-width: 767px) {
  .header__container._active {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 35px;
    flex-direction: column;
    color: #011c40;
    background-color: #f9fbfd;
    z-index: 4;
  }
}
@media (max-width: 767px) {
  .header__container._active .menu__list {
    flex-direction: column;
    padding-top: 75px;
    padding-left: 20px;
    transform: translateX(0px);
    top: 0;
  }
}
.header__container._active .menu__button {
  width: 15px;
  height: 15px;
  position: fixed;
  padding: 5px;
}
.header__container._active .menu__button::before {
  top: calc(50% - 1px);
  transform: rotate(-45deg);
  background-color: #011c40;
}
.header__container._active .menu__button::after {
  bottom: calc(50% - 1px);
  transform: rotate(45deg);
  background-color: #011c40;
}
.header__container._active .menu__button span {
  width: 0;
}

header__container._active:has(.header):before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background: rgba(0, 43, 102, 0.5);
  opacity: 1;
  z-index: -1;
}

.logo__link {
  display: block;
  transition: all 0.3s ease 0s;
}

.logo {
  transition: all 0.3s ease 0s;
}
@media (max-width: 767px) {
  .logo {
    position: absolute;
    top: 20px;
    left: 15px;
  }
}
.logo__link {
  color: inherit;
}
.logo__img {
  width: 100%;
  display: block;
}
@media (max-width: 1023px) {
  .logo__img {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo__img {
    width: 42px;
    height: 22px;
  }
}

.about {
  color: #f9fbfd;
  display: block;
  padding-top: 283px;
  padding-bottom: 283px;
  padding-left: 95px;
  padding-right: 50px;
  background-color: #00306b;
  background: center bottom/cover no-repeat url("../img/background.webp");
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .about {
    background-image: center bottom/cover no-repeat url("../img/background@2.webp");
  }
}
@media (max-width: 1023px) {
  .about {
    box-shadow: none;
    padding-top: 280px;
    padding-bottom: 280px;
    padding-left: 0;
    padding-right: 0;
    background: center bottom/cover no-repeat url("../img/backgroundtablet.webp");
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .about {
    background: center bottom/cover no-repeat url("../img/backgroundtablet@2.webp");
  }
}
@media (max-width: 767px) {
  .about {
    padding-top: 185px;
    padding-bottom: 185px;
    background: center bottom/cover no-repeat url("../img/backgroundmobile.webp");
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .about {
    background: center bottom/cover no-repeat url("../img/backgroundmobile@2.webp");
  }
}
.about h1 {
  margin: 0;
  font-weight: bold;
  margin-bottom: 30px;
  font-size: 54px;
  line-height: 1;
}
@media (max-width: 1023px) {
  .about h1 {
    margin-bottom: 29px;
    font-size: 50px;
  }
}
@media (max-width: 767px) {
  .about h1 {
    margin-bottom: 25px;
    font-size: 45px;
    line-height: 1.25;
  }
}
.about p {
  margin: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 1;
}
@media (max-width: 1023px) {
  .about p {
    font-size: 18px;
  }
}
@media (max-width: 767px) {
  .about p {
    font-size: 16px;
    line-height: 1.4;
  }
}

.features {
  padding-top: 120px;
  padding-bottom: 120px;
  background: center bottom/cover no-repeat #def4fd url("../img/background-adventure.webp");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .features {
    background-image: center bottom/cover no-repeat #def4fd url("../img/background-adventure@2.webp");
  }
}
@media (max-width: 1023px) {
  .features {
    padding-top: 100px;
    padding-bottom: 100px;
    background: center bottom/cover no-repeat #def4fd url("../img/background-adventure-tablet.webp");
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .features {
    background: center bottom/cover no-repeat #def4fd url("../img/background-adventure-tablet@2.webp");
  }
}
@media (max-width: 767px) {
  .features {
    padding-top: 65px;
    padding-bottom: 65px;
    background: center bottom/cover no-repeat #def4fd url("../img/background-adventure-mobile.webp");
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .features {
    background: center bottom/cover no-repeat #def4fd url("../img/backgroundadventure-mobile@2.webp");
  }
}
.features__container {
  display: flex;
  flex-direction: column;
  gap: 60px;
}
@media (max-width: 767px) {
  .features__container {
    gap: 40px;
  }
}
.features h2 {
  text-align: center;
  margin: 0;
}

.features-content__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .features-content__list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .features-content__list {
    grid-template-columns: repeat(1, 1fr);
  }
}

.feature-card {
  display: flex;
  flex-direction: column;
  background-color: #f9fbfd;
  padding: 30px 30px 50px 30px;
  height: 100%;
}
@media (max-width: 1023px) {
  .feature-card {
    padding: 26px 20px 25px 20px;
  }
}
@media (max-width: 767px) {
  .feature-card {
    padding: 25px 20px 25px 20px;
  }
}
.feature-card__img {
  order: -1;
  margin-bottom: 25px;
}
@media (max-width: 1023px) {
  .feature-card__img {
    margin-bottom: 24px;
  }
}
@media (max-width: 767px) {
  .feature-card__img {
    margin-bottom: 25px;
  }
}
.feature-card h3 {
  margin: 0;
  margin-bottom: 20px;
  font-size: 18px;
  line-height: 1.5;
}
@media (max-width: 1023px) {
  .feature-card h3 {
    line-height: 1.4;
  }
}
.feature-card p {
  margin: 0;
  font-size: 14px;
  line-height: 1.4;
}

.menu {
  position: relative;
  overflow: hidden;
}
@media (max-width: 767px) {
  .menu {
    position: absolute;
    max-width: 250px;
  }
}
.menu__button {
  box-sizing: border-box;
  transform: scale(0);
  border: none;
  position: absolute;
  top: 25px;
  right: 15px;
  width: 24px;
  height: 12px;
  background-color: inherit;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}
@media (max-width: 767px) {
  .menu__button {
    transform: scale(1);
    z-index: 5;
  }
}
.menu__button:hover {
  opacity: 0.7;
  transform: scale(0.9);
}
.menu__button span, .menu__button::after, .menu__button::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #f9fbfd;
  transition: all 0.3s ease 0s;
  right: 0;
}
