.card {
  overflow: hidden;
  background-color: #011c40;
  position: relative;
  transition: all 0.3s ease 0s;
  width: 100%;
  aspect-ratio: 260/386;
}
@media (max-width: 767px) {
  .card {
    aspect-ratio: 270/386;
  }
}
.card:hover:before {
  opacity: 1;
}
.card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background: #011c40;
  opacity: 0;
  z-index: 2;
  transition: all 0.3s ease 0s;
}
.card__preview,
.card img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  object-fit: cover;
}
.card__preview::after {
  content: "";
  display: block;
  width: 100%;
  height: 50%;
  position: absolute;
  left: 0;
  bottom: 0;
  background: linear-gradient(rgba(1, 28, 64, 0) 0%, #011C40 100%);
}
.card__content {
  position: absolute;
  color: #f9fbfd;
  padding: 29px 24px 25px 26px;
  height: 100%;
  width: 100%;
  z-index: 3;
  transform: translate(6px, 79%);
  transition: all 0.3s ease 0s;
}
@media (max-width: 1023px) {
  .card__content {
    padding: 35px;
  }
}
@media (max-width: 767px) {
  .card__content {
    padding: 35px 25px;
  }
}
.card h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
  margin: 0;
  margin-bottom: 14px;
}
.card dl {
  margin: 0;
  opacity: 0;
}
.card dt {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.4;
  margin: 0;
  margin-bottom: 1px;
}
.card dd {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  margin: 0;
  margin-bottom: 12px;
}
.card__button {
  margin-top: 12px;
}

.card:hover::before {
  opacity: 1;
}
.card:hover .card__content {
  transform: translate(0, 0);
}
.card:hover dl {
  opacity: 1;
}

