.header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;

  &__overlay {
    @include vp-767 {
      position: absolute;
      top: 0;
      left: 0;
      display: none;
      width: 100%;
      height: 100vh;
      background: rgba(0, 43, 102, 0.5);
      opacity: 1;
      z-index: 2;

      &._active {
        display: block;
      }
    }
  }

  &__container {
    transition: all 0.3s ease 0s;
    transform-origin: right top;
    min-width: 38px;
    display: flex;
    justify-content: space-between;
    padding-top: 35px;
    padding-bottom: 20px;
    color: $color-text-light;
    z-index: 3;

    @include vp-1023 {
      padding-top: 20px;
    }

    &._active {
      transition: all 0.3s ease 0s;
      transform-origin: right;

      @include vp-767 {
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 35px;
        flex-direction: column;
        color: $color-text-dark;
        background-color: $color-default-white;
        z-index: 4;
      }
    }

    &._active .menu__list {
      @include vp-767 {
        flex-direction: column;
        padding-top: 75px;
        padding-left: 20px;
        transform: translateX(0px);
        top: 0;
      }
    }

    &._active .menu__button {
      width: 15px;
      height: 15px;
      position: fixed;
      padding: 5px;
    }

    &._active .menu__button::before {
      top: calc(50% - 1px);
      transform: rotate(-45deg);
      background-color: $color-bg-dark;
    }

    &._active .menu__button::after {
      bottom: calc(50% - 1px);
      transform: rotate(45deg);
      background-color: $color-bg-dark;
    }

    &._active .menu__button span {
      width: 0;
    }
  }
}

header__container._active:has(.header) {
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: rgba(0, 43, 102, 0.5);
    opacity: 1;
    z-index: -1;
  }
}

.logo__link {
  display: block;
  transition: all 0.3s ease 0s;
}
