.catalog {
  padding-top: 95px;
  padding-bottom: 120px;

  @include vp-1023 {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    padding-top: 65px;
    padding-bottom: 65px;
  }

  h2 {
    margin: 0;
    margin-bottom: 60px;
    text-align: center;
    font-size: 28px;
    line-height: 1.5;

    @include vp-1023 {
      margin-bottom: 40px;
      font-size: 26px;
    }

    @include vp-767 {
      margin-bottom: 40px;
      font-size: 24px;
    }
  }
}
