.cards-catalog {
  padding: 0 15px;

  @include vp-767 {
    padding: 0 10px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap:30px;
    list-style: none;
    margin: 0;
    padding: 0;

    @include vp-1023 {
      grid-template-columns: repeat(2, 1fr);
    }

    @include vp-767 {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}
