.social {

  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 20px 27px;
    max-width: 120px;

    @include vp-1023 {
      gap: 20px 22px;
    }
  }

  a {
    color: inherit;
    padding: 1px;
    display: grid;
    place-content: center;
    transition: all 0.3s ease 0s;

    &:hover {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.7;
      transform: scale(0.9);
    }
  }

  svg {
    @include vp-1023 {
      width: 16px;
    }
  }
}
